.chamada {
    display: flex;
    align-content: center;
    align-items: center;
    width: 100%;
    overflow: hidden;
    background-color: var(--cinza-escuro);
    padding-top: 1rem;
}

.chamada__carousel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 30rem;
    width: 100%;
}

.chamada__slider {
    display: flex;
    align-items: center;
    height: 28rem;
    width: 18rem;
}

.chamada__slide {
    width: 100%;
}

.chamada__div { 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.chamada__container {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.chamada__imagem {
    width: 100%;
    max-width: calc(18rem - 20px);
    border: 10px solid var(--cinza-medio);
    object-fit: cover;
}

.chamada__titulo {
    width: 100%;
    max-width: calc(18rem - 20px);
    color: var(--branco);
    font-weight: 700;
    font-size: 1.5rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    text-transform: uppercase;
    text-align: center;
}

.chamada__caption {
    width: 100%;
    max-width: calc(18rem - 20px);
    margin-bottom: 1rem;
    font-size: 1rem;
    color: var(--branco);
    font-weight: 400;
    text-align: center;
    margin-left: 0.625rem;
}

.chamada__setas {
    position: relative;
    display: flex;
    top: 0;
    border: none;
    background-color: transparent;
    justify-content: space-evenly;
    align-items: center;
    vertical-align: center;
    padding-top: 2rem;
    padding-bottom: 2rem;
    width: 100vw;
    max-width: 280px;
}

.chamada__seta-esquerda {
    background-color: transparent;
    background-image: url(../img/icons/seta-esquerda.svg);
    background-repeat: no-repeat;
    border: none;
    margin-right: 2rem;
}

.chamada__seta-direita {
    background-color: transparent;
    background-image: url(../img/icons/seta-direita.svg);
    background-repeat: no-repeat;
    border: none;
    margin-left: 2rem;
}

@media screen and (min-width: 768px) {
    .chamada__carousel{
        padding-bottom: 2rem;
    }
    .chamada__container{
        height: 28rem;
    }
    .chamada__setas{
        position: absolute;
        justify-content: space-around;
        bottom: 15%;
        max-width: 100%;
    }
    .chamada__seta-esquerda {
        margin-right: 14rem;
    }
    .chamada__seta-direita {
        margin-left: 14rem;
    }
}
@media screen and (min-width: 1024px) {
    .chamada__slider {
        width: 54rem;
    }
    .chamada__div {
        flex-direction: row;
        width: 24.5%;
    }
    .chamada__container {
        margin-right: 2rem;
    }
    .chamada__seta-esquerda {
        margin-right: 28rem;
    }
    .chamada__seta-direita {
        margin-left: 28rem;
    }
}

@media screen and (min-width: 2560px) {
    .chamada {
        width: 100%;
        height: 50rem;
    }
    .chamada__carousel {
        width: 100%;
        height: 70rem;
    }
    .chamada__slider {
        width: 118rem;
        height: 70rem;
    }
    .chamada__slide {
        width: 120rem;
        height: 70rem;
    }
    .chamada__div {
        width: 120rem;
        height: 70rem;
    }
    .chamada__container {
        width: 60rem;
        height: 40rem;
        align-items: center;
    }
    .chamada__imagem {
        width: calc(36rem - 20px);
        max-width: calc(36rem - 20px);
    }
    .chamada__titulo {
        width: calc(36rem - 20px);
        max-width: calc(36rem - 20px);
        font-size: 3rem;
    }
    .chamada__caption {
        width: calc(36rem - 20px);
        max-width: calc(36rem - 20px);
        font-size: 2rem;
    }
    .chamada__setas {
        justify-content: space-between;
    }
    .chamada__seta-esquerda {
        width: 10rem;
        height: 10rem;
        background-size: 10rem;
        margin-left: 7rem;
    }
    .chamada__seta-direita {
        width: 10rem;
        height: 10rem;
        background-size: 10rem;
        margin-right: 7rem;
    }
}