.projetos {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--cinza-projetos);
    padding-top: 1rem;
    padding-bottom: 1rem;
    width: 100%;
    height: 100%;
}

.projetos__titulo {
    color: var(--cinza-medio);
    font-weight: 700;
    font-size: 1.5rem;
    padding-top: 1.5rem;
    margin-bottom: 1.5rem;
    text-align: left;
}

.projetos__figure-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.projetos__imagem {
    width: calc(100% - 20px);
    max-width: 18rem;
    margin-bottom: 1rem;
    border: 10px solid var(--cinza-medio);
}

.projetos__texto {
    width: 100%;
    max-width: 18rem;
    text-align:center;
    font-weight: 400;
    font-size: 1rem;
    color: var(--cinza-medio);
    padding-bottom: 1rem;
}

.content{
    padding: 0 18px;
    background-color: white;
  }

.accordion {
    width: calc(100% - 2rem);
    border-radius: 5px;
    border: 1px solid #bebebe;
    margin-left: 1rem;
    margin-right: 1rem;
}

.accordion .accordion-item:first-child .title {
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.accordion .accordion-item:last-child .title {
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.accordion .accordion-item .title {
    background-color: #6f6f6f;
    color: #ffffff;
    cursor: pointer;
    font-weight: 700;
    font-size: 1rem;
    padding: 1.5rem;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    -webkit-transition: 0.4s;
    -o-transition: 0.4s;
    transition: 0.4s;
    margin-bottom: 0.1rem;
}
  
.accordion .accordion-item .title:after {
    content: url('../img/icons/seta-para-baixo-branca.svg');
    color: #ffffff;
    float: right;
    margin-left: 5px;
}
  
.accordion .accordion-item .panel {
    max-height: 0;
    -webkit-transition: max-height 0.15s ease-out;
    -o-transition: max-height 0.15s ease-out;
    transition: max-height 0.15s ease-out;
    overflow: hidden;
}
  
.accordion .accordion-item.active .title {
    background-color: #6f6f6f;
}

.accordion .accordion-item.active .title:after {
    content: url('../img/icons/seta-para-baixo-branca.svg');
    transform: rotate(180deg);
}
  
.accordion .accordion-item.active .panel {
    max-height: 500px;
    -webkit-transition: max-height 0.25s ease-in;
    -o-transition: max-height 0.25s ease-in;
    transition: max-height 0.25s ease-in;
}

@media screen and (min-width: 768px) {
    .projetos__figure-container {
        flex-direction: row;
        align-items: center;
        justify-content: left;
    }
    .projetos__imagem {
        width: 25%;
    }
    .projetos__texto {
        width: 75%;
        max-width: 100%;
        text-align:left;
        margin-left: 1rem;
        font-size: 1.1rem;
    }
}



  